import React, { Component, PropTypes } from 'react';
import { Router, Route, Link ,hashHistory} from 'react-router';

import { Icon,Modal,WhiteSpace } from 'antd-mobile';

/* 自定义验证 start*/
import Commonflex from '../common_flex/common_flex';
import Validate from '../../widget/react-validate/react-validate';
/* 自定义验证 end*/

import formMethod from './form_method';

import './form.scss'

class Resetpassword extends Component{
    constructor(){
        super();
    }
    componentWillMount(){

        //在此处初始化状态
        this.state={
            form: {
                password: '',
                confirmPassword: '',
            }
        }
    }

    componentDidMount(){
        // 存储 start
        componentStore.set(this);
        // 存储 end

    };
    componentWillUnmount(){
        // 清除 start
        componentStore.clear(this);
        // 清除 end
    };

    render(){
        var that = this;
        return (
            <Commonflex $id="zhuce" title="修改密码">
                <div className="form-wrap">

                    <Validate ref="validator" $id="myflex_validate" onError={(obj)=>{

                        formMethod.errorStyle.right.showError(obj);

                    }}>
                        <div className="form-content">
                            <div className="form-item">
                                <div className="form-icon-box">
                                    <Icon type={require('../../../image/svg/password.svg')} />
                                </div>
                                <div className="form-input-box">
                                    <input type="password" value={that.state.form.password}
                                           onChange={(obj)=>{
                                               that.state.form.password = obj.target.value;
                                               componentStore.update(that,that.state)
                                           }}
                                           onFocus={(obj)=>{
                                               formMethod.errorStyle.right.hideError(obj);
                                           }}
                                           onBlur={(obj)=>{
                                               that.state.form.isOk = that.refs.validator.validateOne('password');
                                               componentStore.update(that,that.state);
                                           }}
                                           data-validName="password"
                                           data-validRules={[{
                                               name: 'require'
                                           },{
                                               name: 'password'
                                           }]}
                                           data-validMsgPrefix={'新密码：'}
                                           placeholder="请输入新密码"
                                    />
                                </div>
                            </div>
                            <div className="form-item">
                                <div className="form-icon-box">
                                    <Icon type={require('../../../image/svg/password.svg')} />
                                </div>
                                <div className="form-input-box">
                                    <input type="password"  value={that.state.form.confirmPassword}
                                           onChange={(obj)=>{
                                               that.state.form.confirmPassword = obj.target.value;
                                               componentStore.update(that,that.state)
                                           }}
                                           onFocus={(obj)=>{
                                               formMethod.errorStyle.right.hideError(obj);
                                           }}
                                           onBlur={(obj)=>{
                                               that.state.form.isOk = that.refs.validator.validateOne('confirmPassword');
                                               componentStore.update(that,that.state);
                                           }}
                                           data-validName="confirmPassword"
                                           data-validRules={[{
                                               name: 'require'
                                           },{
                                               name: 'equal',
                                               params: {
                                                   name: 'password',
                                                   chinese: '密码'
                                               }
                                           }]}
                                           data-validMsgPrefix={'确认密码：'}
                                           placeholder="请再次输入新密码"
                                    />
                                </div>
                            </div>

                        </div>

                        <WhiteSpace size="xl"/>
                        <WhiteSpace size="lg"/>
                        <WhiteSpace size="lg"/>

                        <div className="form-detect">
                            <input type="button" value="设置新密码" className="form-register-btn" onTouchEnd={()=>{

                                Utils.eventHanlder(function(){

                                    var res = that.refs.validator.validateAll();
                                    if(res){
                                        Modal.alert('提示','重置密码成功', [
                                            { text: '取消'},
                                            { text: '确定'}
                                        ])
                                    }
                                })


                            }}/>
                        </div>

                    </Validate>
                </div>
            </Commonflex>

        )
    }

}

export default Resetpassword;



